.emoji {
    font-size: 80px;
}

.emoji span:hover {
    cursor: pointer;
}

.emoji:hover {
    cursor: default;
}

.loading {
    animation: mover;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
}

@keyframes mover {

    0% {
        transform: translateY(0px);
    }

    12.5% {
        transform: translateY(5px);
    }

    25% {
        transform: translateY(10px);
    }

    37.5% {
        transform: translateY(5px);
    }

    50% {
        transform: translateY(0px);
    }

    62.5% {
        transform: translateY(-5px);
    }

    75% {
        transform: translateY(-10px);
    }

    87.5% {
        transform: translateY(-5px);
    }

    100% {
        transform: translateY(0px);
    }
}